<template>
    <div class="salary-trend"></div>
</template>

<script>
export default {
    name: 'SalaryTrend',
    props: {
        data: Object
    },
    data() {
        return {
            myChart: null
        }
    },
    methods: {
        setChart() {
            if (!this.data) return
            
            const option = {
                title: {
                    text: '薪资分布趋势',
                    textStyle: {
                        color: '#75deef',
                        fontSize: 14,
                    },
                    right: '5%',
                    top: '0%',
                },
                tooltip: {
                    trigger: 'axis',
                    backgroundColor: '#11367a',
                    textStyle: {
                        color: '#6dd0e3',
                        fontSize: 10
                    }
                },
                grid: {
                    left: '10%',
                    right: '5%',
                    top: '20%',
                    bottom: '15%'
                },
                xAxis: {
                    type: 'category',
                    data: this.data.ranges,
                    axisLine: {
                        lineStyle: {
                            color: '#1a3c58'
                        }
                    },
                    axisLabel: {
                        color: '#75deef',
                        fontSize: 10
                    }
                },
                yAxis: {
                    type: 'value',
                    name: '职位数量',
                    nameTextStyle: {
                        color: '#75deef',
                        fontSize: 10
                    },
                    axisLine: {
                        lineStyle: {
                            color: '#1a3c58'
                        }
                    },
                    axisLabel: {
                        color: '#75deef',
                        fontSize: 10
                    },
                    splitLine: {
                        lineStyle: {
                            color: 'rgba(26,60,88,0.5)'
                        }
                    }
                },
                series: [{
                    name: '职位数量',
                    type: 'line',
                    smooth: true,
                    data: this.data.values,
                    areaStyle: {
                        color: {
                            type: 'linear',
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [{
                                offset: 0, color: 'rgba(44,168,254,0.8)'
                            }, {
                                offset: 1, color: 'rgba(44,168,254,0.1)'
                            }]
                        }
                    },
                    lineStyle: {
                        color: '#2ca8fe',
                        width: 2
                    },
                    itemStyle: {
                        color: '#2ca8fe'
                    }
                }]
            }
            
            if (!this.myChart) this.myChart = this.$echarts(this.$el)
            this.myChart.clear()
            this.myChart.resize()
            this.myChart.setOption(option)
        }
    },
    watch: {
        data: {
            handler() {
                this.setChart()
            },
            deep: true
        }
    },
    mounted() {
        this.setChart()
    }
}
</script>

<style lang="less" scoped>
.salary-trend {
    width: 100%;
    height: 100%;
}
</style>